export type Pizza = {
  id: string;
  name: string;
  types: number[];
  price: number;
  count: number;
  imageUrl: string;
  sizes: number[];
}

export enum Status {
  LOADING = 'loading',
  SUCCESS = 'success',
  ERROR = 'error',
}

export interface PizzaSliceState {
  items: Pizza[];
  status: Status;               //'loading' | 'success' | 'error';     ->               'enum Status' (  )
}

export type SeachPizzaParams = {
  sortBy: string;
  category: string;
  order: string;
  search: string;
  currentPage: string;
}                             //   ,                (string, number, ...)

