import React from "react";
import useWhyDidYouUpdate from 'ahooks/lib/useWhyDidYouUpdate';

type CategoriesProps = {
  value: number;
  onClickCategory: (idx: number) => void;             //     TS ,  idx      ,             (map)
                                                      //       () ,       -> 'onClickCategory?: ...'
}

const categories = ['', '', '', '', '', ''];

const Catigories: React.FC<CategoriesProps> = React.memo(({ value, onClickCategory}) => {           //React.memo        ,     ..
  useWhyDidYouUpdate('Categories', { value, onClickCategory});


  // const onClickCategory = (index) => {
  //   setActiveIndex(index);
  //   // console.log(1213212123113);
  //   //        
  // };

  return (
    <div className="categories">
      <ul>
        {
          categories.map((categoryName, i) => (<li key={i} onClick={() => onClickCategory(i)} className={value === i ? 'active' : ''}>{categoryName}</li>))
        }
      </ul>
    </div>
  );
});

export default Catigories;